import { useState, useEffect, Fragment } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay, EffectFade } from "swiper/modules";
import { useSelector } from "react-redux"
import gsap from 'gsap';
import 'swiper/css';
import "swiper/css/pagination";
import 'swiper/css/effect-fade';

function SwiperJS(){
	let [mainSwiper, setSwiper]=useState(null);
	let [mainSwiperFlag, setFlag]=useState(true);
	let [tween, setTween]=useState(null);

	let screensource = useSelector((state) => state.screensource )
	console.log(screensource);
	let skills = useSelector((state) => state.skills )

	useEffect(() => {
		if(mainSwiper === null) return;

		let [prevBtn, nextBtn]=document.querySelector(".direction").children;
		let pausePlayBtn=document.getElementById("pause_play");
		prevBtn.addEventListener("click", e => {
			e.preventDefault();
			mainSwiper.slidePrev();
		});
		nextBtn.addEventListener("click", e => {
			e.preventDefault();
			mainSwiper.slideNext();
		});

		pausePlayBtn.addEventListener("click", e => {
			e.preventDefault();
			if(pausePlayBtn.classList.contains("play")){
				pausePlayBtn.classList.remove("play");
				pausePlayBtn.classList.add("pause"); // play
				mainSwiper.autoplay.start();
				tween.seek(0);
				tween.play();
				setFlag(true);
			}
			else if(pausePlayBtn.classList.contains("pause")){
				pausePlayBtn.classList.remove("pause");
				pausePlayBtn.classList.add("play"); // pause
				mainSwiper.autoplay.stop();
				tween.pause();
				setFlag(false);
			}
		});
	}, [mainSwiper]);

	let slideChange=(swiper, tween) => {
		let current=document.getElementById("current");
		let total=document.getElementById("total");
		let [prevBtn, nextBtn]=document.querySelector(".direction").children;

		current.innerText=swiper.realIndex+1;
		total.innerText=swiper.slides.length;
		let n=swiper.realIndex;
		if(n === 0){
			prevBtn.style.display="none";
		}
		else if(n === 4){
			nextBtn.style.display="none";
		}
		else{
			prevBtn.style.display="block";
			nextBtn.style.display="block";
		};

		let texBox=document.querySelectorAll(".text_box");
		for(let i=0; i<texBox.length; i++){
			let [title, text]=texBox[i].children;
			if(i === n){
				setTimeout(function(){
					title.classList.add("active");
					text.classList.add("active");
				}, 100)
			}
			else {
				title.classList.remove("active");
				text.classList.remove("active");
			}
		}

		if(mainSwiperFlag){
			tween.seek(0);
			tween.play();
		}
		else{
			tween.seek(0);
			tween.pause();
		}
	};

	return(
		<Fragment>
			<Swiper
				loop={false}
				speed={1000}
				autoplay={{
					delay: 5000,
					disableOnInteraction: false
				}}
				effect="fade"
				onSwiper={swiper => {
					let movingbar=document.querySelector(".moving");
					let tweenCopy=gsap.to(movingbar, {width: "100%", duration: 5});
					setSwiper(swiper);
					setTween(tweenCopy);
					slideChange(swiper, tweenCopy);
				}}
				onSlideChange={swiper => {
					slideChange(swiper, tween);
				}}
				modules={[Autoplay, EffectFade]}
				className="swiper-container mainSwiper"
			>
				{
					screensource.map(function(d,i){
						return(
							<SwiperSlide key={i}>
								{
									<>
										<img src={ screensource[i] }></img>
										<div className='text_box'>
											<h1 className='title'>{ skills[i].name }</h1>
											<p className='text'>{ skills[i].detail }</p>
										</div>
									</>
								}
							</SwiperSlide>
						)
					})
				}
			</Swiper>
			<div className="direction">
				<a href="" className="prev">
					<p>PREV</p>
					<span className="prev_top"></span>
					<span className="prev_bottom"></span>
				</a>
				<a href="" className="next">
					<p>NEXT</p>
					<span className="next_top"></span>
					<span className="next_bottom"></span>
				</a>
			</div>
			<div className="controller">
				<a href="" id="pause_play" className="pause"></a>
				<div className="progressbar"><span className='moving'></span></div>
			</div>
			<div className="account">
				<span className="current" id="current">current</span> / <span className="total" id="total">total</span>
			</div>
		</Fragment>
	);
}

export default SwiperJS;