import { configureStore, createSlice } from '@reduxjs/toolkit'

let skills = createSlice({
    name : 'skills',
    initialState : 
        [
            {name : "Create Semantic Pages", detail : "Create semantic pages that increase web accessibility"},
            {name : "Javascript And Libraries", detail : "Using JavaScript implementations and libraries"},
            {name : "Git System Using Github", detail : "Git system collaboration using GitHub"},
            {name : "User Interface Using React", detail : "User interface implementation using React"},
            {name : "ReactJS With Many Libraries", detail : "User interface implementation using React"}
        ]
})

let spec = createSlice({
    name : 'spec',
    initialState : 
        [   
            {name : "HTML", detail : "HTML5 DTD 문법 준수"},
            {name : "CSS", detail : "웹 접근성을 높인 Semantic Page 작성, IR 기법을 이용한 스타일 구현 모바일 스타일 구현"},
            {name : "SASS", detail : "전처리 기능을 CSS 프로그래밍 구현"},
            {name : "JavaScript", detail : ["jQuery 플러그인 활용 및 구현", "외부 API 구현(Google Map, SwiperJS)", "EcmaScript 2022 기반 Native JavaScript 구현"]},
            {name : "SCS", detail : "GitHub를 활용한 Git 시스템 협업"},
            {name : "UI Prototype", detail : "Figma를 이용한 사용자 가이드 작성"},
            {name : "ReactJS", detail : "React를 활용한 사용자 HTML 동적 바인딩 axios, Fetch API, Router, Redux 라이브러리 사용"},
            {name : "JSP", detail : "Java 웹 애플리케이션 구현 기본 include와 제어문 작성"}
        ]
})

let screensource =createSlice({
    name : "screensource",
    initialState : ["./images/work1.jpg","./images/work2.jpg","./images/work3.jpg","./images/work4.jpg","./images/work5.jpg"]
})

export default configureStore({
    reducer: { 
        skills : skills.reducer,
        spec : spec.reducer,
        screensource : screensource.reducer
    }
});