import { useEffect, useState } from 'react';
import { useSelector } from "react-redux"
import SwiperJS from './swiperJS';
import About from './about';

function Section(){
  let spec = useSelector((state) => state.spec )
  useEffect(()=>{
    let number=document.querySelectorAll(".number");
    let numberLi = Array.from(number);
    for(let i=0; i<numberLi.length; i++){
      numberLi[i].addEventListener("mouseenter",()=>{
        number[i].classList.add("active");
        setTimeout(()=>{
          number[i].classList.remove("active");
        },450);
      })
    }
  })
  return(
    <>
      <div className='main_slider'>
        <SwiperJS />
      </div>
      <About />
      <section id='section'>
        {
          spec.map((d,i)=>{
            return(
              <section className='page' key={i}>
                <div className='number'>
                  { "0"+(i+1) }
                </div>
                <div className='spec'>
                  <p className='title'>{ spec[i].name }</p>
                  <span>{ spec[i].detail }</span>
                </div>
              </section>
            )
          })
        }
      </section>
    </>
  );
}

export default Section;